'use client';

import { useCallback, useEffect, useMemo, useState } from 'react';
import { useAccount, useReadContract, usePublicClient } from 'wagmi';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { sdk } from '@farcaster/miniapp-sdk';
import { COLLECTION_ABI } from '@/lib/abi/collection';
import { ERC721_EVENTS } from '@/lib/abi/erc721Events';
import { CONTRACT_ADDRESS } from '@/lib/constants';
import { ipfsToHttp } from '@/lib/ipfs';

type MintItem = {
  tokenId: bigint;
  metadataUrl: string;
  imageUrl?: string;
  name?: string;
  error?: string;
};

const FROM_BLOCK_ENV = process.env.NEXT_PUBLIC_DEPLOY_BLOCK;

function parseFromBlock(latest: bigint) {
  if (!FROM_BLOCK_ENV) {
    // fallback: آخر ~400k بلوك (تقريبًا بضعة أسابيع)
    const span = 400_000n;
    return latest > span ? latest - span : 0n;
  }
  const n = BigInt(Number(FROM_BLOCK_ENV));
  return n > 0n ? n : 0n;
}

async function getLogsChunked(opts: {
  publicClient: ReturnType<typeof usePublicClient>;
  address: `0x${string}`;
  to: `0x${string}`;
  fromBlock: bigint;
  toBlock: bigint;
  step?: bigint;
}) {
  const { publicClient, address, to, fromBlock, toBlock } = opts;
  const step = opts.step ?? 50_000n; // حجم الدفعة
  let start = fromBlock;
  const all: any[] = [];
  // تقسيم النطاق إلى دفعات
  while (start <= toBlock) {
    const end = start + step > toBlock ? toBlock : start + step;
    // @ts-ignore
    const logs = await publicClient!.getLogs({
      address,
      event: ERC721_EVENTS.Transfer as any,
      fromBlock: start,
      toBlock: end,
      // @ts-ignore
      args: { to },
    });
    all.push(...logs);
    start = end + 1n;
  }
  return all;
}

export default function MyNftsPage() {
  const { address, isConnected } = useAccount();
  const publicClient = usePublicClient();

  // Ready call
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const appUrl = process.env.NEXT_PUBLIC_APP_URL;
    const sameOrigin = !appUrl || window.location.origin === appUrl;
    if (sameOrigin) sdk.actions.ready().catch(console.error);
  }, []);

  const BASE_URI =
    'ipfs://bafybeif4nhbmbypovzvr2wz5p2kfm2wy2simfbbxawmkk2j3cjp4guxsgy/';

  const { data: mintedOfMe } = useReadContract({
    address: CONTRACT_ADDRESS,
    abi: COLLECTION_ABI,
    functionName: 'mintedOf',
    args: [address ?? '0x0000000000000000000000000000000000000000'],
  });

  const [items, setItems] = useState<MintItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [logError, setLogError] = useState<string | null>(null);

  const load = useCallback(async () => {
    if (!isConnected || !address || !publicClient) return;
    setLoading(true);
    setLogError(null);
    try {
      const latest = await publicClient.getBlockNumber();
      const fromBlock = parseFromBlock(latest);
      const logs = await getLogsChunked({
        publicClient,
        address: CONTRACT_ADDRESS,
        to: address,
        fromBlock,
        toBlock: latest,
        step: 40_000n,
      });

      const tokenIds = Array.from(
        new Set(logs.map((l: any) => l.args?.tokenId as bigint).filter(Boolean))
      );

      const metas = tokenIds.map((tid) => {
        const metadataUrl = ipfsToHttp(`${BASE_URI}${tid}.json`);
        return { tokenId: tid, metadataUrl } as MintItem;
      });

      const withMeta = await Promise.all(
        metas.map(async (m) => {
          try {
            const res = await fetch(m.metadataUrl, { cache: 'no-store' });
            if (!res.ok) throw new Error(`HTTP ${res.status}`);
            const json = await res.json();
            const img = ipfsToHttp(json.image || json.image_url || '');
            return { ...m, imageUrl: img, name: json.name };
          } catch (e: any) {
            return { ...m, error: `Metadata fetch failed: ${e?.message || e}` };
          }
        })
      );

      setItems(withMeta);
    } catch (e: any) {
      setLogError(e?.message || String(e));
    } finally {
      setLoading(false);
    }
  }, [isConnected, address, publicClient]);

  useEffect(() => {
    load();
  }, [load]);

  const mintedCount = useMemo(
    () =>
      (mintedOfMe as bigint | undefined)?.toString() ??
      (isConnected ? '0' : '—'),
    [mintedOfMe, isConnected]
  );

  return (
    <main className="min-h-screen bg-neutral-950 text-white">
      <header className="flex items-center justify-between p-4 border-b border-neutral-800">
        <div className="text-lg font-semibold">RACCOON-MINT — My NFT</div>
        <ConnectButton />
      </header>

      <section className="max-w-3xl mx-auto p-6 grid gap-6">
        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-bold">Your Minted NFTs</h2>
            <button
              onClick={load}
              className="text-sm rounded-lg border border-neutral-700 px-3 py-1 hover:bg-neutral-800"
              disabled={loading}
            >
              {loading ? 'Refreshing…' : 'Refresh'}
            </button>
          </div>
          {!isConnected ? (
            <p className="mt-2 text-sm text-neutral-300">
              Connect your wallet to see your mints.
            </p>
          ) : (
            <p className="mt-2 text-sm text-neutral-300">
              Minted by you:{' '}
              <span className="font-mono text-white">{mintedCount}</span>
              {FROM_BLOCK_ENV && (
                <span className="text-neutral-500"> (from block {FROM_BLOCK_ENV})</span>
              )}
            </p>
          )}
          {logError && (
            <p className="mt-2 text-amber-300 text-sm">
              ⚠️ Logs error: {logError}
            </p>
          )}
        </div>

        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-4">
          {loading ? (
            <p className="text-sm text-neutral-300">Loading your NFTs…</p>
          ) : items.length === 0 ? (
            <p className="text-sm text-neutral-300">
              {isConnected
                ? 'No minted NFTs found via Transfer logs.'
                : 'Connect wallet to view.'}
            </p>
          ) : (
            <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
              {items.map((it) => (
                <article
                  key={it.tokenId.toString()}
                  className="rounded-xl overflow-hidden border border-neutral-800 bg-neutral-900"
                >
                  <div className="aspect-square bg-neutral-800 flex items-center justify-center">
                    {it.imageUrl ? (
                      // eslint-disable-next-line @next/next/no-img-element
                      <img
                        src={it.imageUrl}
                        alt={it.name ?? `#${it.tokenId.toString()}`}
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <span className="text-xs text-neutral-400 p-2 text-center">
                        {it.error ? it.error : 'No image'}
                      </span>
                    )}
                  </div>
                  <div className="p-3 text-sm">
                    <div className="font-mono">#{it.tokenId.toString()}</div>
                    {it.name && <div className="text-neutral-300">{it.name}</div>}
                    <a
                      className="text-cyan-300 text-xs hover:underline"
                      href={`https://ipfs.io/ipfs/${BASE_URI.replace('ipfs://', '')}${it.tokenId}.json`}
                      target="_blank"
                      rel="noreferrer"
                    >
                      Metadata →
                    </a>
                  </div>
                </article>
              ))}
            </div>
          )}
        </div>

        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <a
            className="text-cyan-300 hover:underline"
            href={`https://basescan.org/address/${CONTRACT_ADDRESS}`}
            target="_blank"
            rel="noreferrer"
          >
            View contract on BaseScan →
          </a>
        </div>
      </section>
    </main>
  );
}
